.hero {
    padding-top: 80px;
    /* Add padding to account for fixed header */
    min-height: 100vh;
    /* Make hero take up full viewport height */
    background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    text-align: center;
}

.hero-content {
    max-width: 800px;
    padding: 0 20px;
}

.hero h1 {
    font-size: 3.5rem;
    margin-bottom: 20px;
    font-weight: 700;
}

.hero h2 {
    font-size: 1.8rem;
    margin-bottom: 30px;
    font-weight: 400;
    color: #e2e2e2;
}

.hero-buttons {
    display: flex;
    justify-content: center;
    gap: 20px;
}

.btn {
    display: inline-block;
    padding: 12px 28px;
    border-radius: 4px;
    font-weight: 600;
    text-decoration: none;
    transition: all 0.3s;
    font-size: 1.1rem;
}

.btn-primary {
    background-color: #4cc9f0;
    color: #1a1a2e;
}

.btn-primary:hover {
    background-color: #3db8e0;
    transform: translateY(-2px);
}

.btn-secondary {
    border: 2px solid #4cc9f0;
    color: #4cc9f0;
}

.btn-secondary:hover {
    background-color: rgba(76, 201, 240, 0.1);
    transform: translateY(-2px);
}

@media (max-width: 768px) {
    .hero h1 {
        font-size: 2.5rem;
    }

    .hero h2 {
        font-size: 1.4rem;
    }

    .hero-buttons {
        flex-direction: column;
        align-items: center;
    }
}