.container {
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.project-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 1.5rem;
    width: 100%;
}

.project-card {
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    margin-bottom: 25px;
    background-color: white;
}

.project-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.15);
}

.project-image {
    width: 100%;
    height: 220px;
    object-fit: cover;
    border-bottom: 1px solid #f0f0f0;
}

.project-content {
    padding: 20px;
}

.project-title {
    font-size: 1.5rem;
    margin-bottom: 12px;
    color: #333;
    font-weight: 600;
}

.project-description {
    color: #555;
    margin-bottom: 18px;
    line-height: 1.6;
}

.project-tech {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    margin-bottom: 20px;
}

.tech-tag {
    background-color: #f5f5f5;
    padding: 5px 10px;
    border-radius: 20px;
    font-size: 0.85rem;
    color: #333;
    font-weight: 500;
}

.project-links {
    display: flex;
    gap: 15px;
}

.project-link {
    padding: 8px 16px;
    border-radius: 4px;
    font-weight: 500;
    transition: all 0.2s;
    text-decoration: none;
    display: inline-block;
}

.demo-link {
    background-color: #007bff;
    color: white;
}

.demo-link:hover {
    background-color: #0069d9;
}

.code-link {
    background-color: #f8f9fa;
    color: #333;
    border: 1px solid #dee2e6;
}

.code-link:hover {
    background-color: #e2e6ea;
}